import React, { useEffect, useState } from 'react'
import { useSelector } from 'react-redux'
import { setAuctionFee } from '../../services/Web3/token'
import Loading from '../../components/Loading'

const Setting = () => {
  const accessToken = useSelector((state) => state.user.accessToken)
  const user = useSelector((state) => state.user.user)
  const [loading, setLoading] = useState(true)
  const [formData, setFormData] = useState({
    fee: 5,
    tokenFee: 3,
    withdraw: '',
    price: 0,
    currency: 'BNB'
  })

  useEffect(() => {
    console.log('use effect')
    ;(async () => {
      try {
        if (accessToken) {
          setLoading(false)
        }
      } catch (err) {
        console.log(err)
      }
    })()
  }, [accessToken])

  const handleSubmitSetFee = async () => {
    let setFeeTx = await setAuctionFee(formData.fee, formData.tokenFee)
    console.log(setFeeTx)
  }
  const handleChange = (e) => {
    setFormData({ ...formData, [e.target.name]: e.target.value })
  }

  if (!user) {
    return (
      <div className="middle-text">
        <div>Login </div>
      </div>
    )
  }

  if (user && user.role !== 'admin') {
    return (
      <div className="middle-text">
        <div>Be a admin</div>
      </div>
    )
  }

  if (loading) {
    return <Loading />
  }

  return (
    <div className="container">
      <div>
        <h1 style={{ textAlign: 'center', margin: '5% 0' }}>Setting</h1>
      </div>

      <div style={{}}>
        <div class="row">
          <div class="col-md-6">
            <div class="mb-3">
              <div class="row">
              <div class="col-md-12">
                <h2>Change Fee</h2>
                </div>
                <div class="col-md-4">
                <label class="form-label">Change Fee</label>
                  <input
                    type="text"
                    class="form-control"
                    placeholder="fee"
                    name="fee"
                    value={formData.fee}
                    onChange={handleChange}
                  />
                </div>
                <div class="col-md-4">
                <label class="form-label">Change Token Fee</label>
                  <input
                    type="text"
                    name="tokenFee"
                    class="form-control"
                    placeholder="token fee"
                    value={formData.tokenFee}
                    onChange={handleChange}
                  />
                </div>
                <div class="col-md-4">
                  <br/>
                <button class="btn btn-primary" onClick={handleSubmitSetFee}>
                Change
              </button>
                </div>
                
              </div>

             <br/>
             <br/>
             <br/>

              <div class="row">
              <div class="col-md-12">
                <h2>Withdraw Price</h2>
                </div>
                <div class="col-md-12">
                <label class="form-label">Withdraw Address</label>
                  <input
                    type="text"
                    class="form-control"
                    placeholder="withdraw address"
                    name="withdraw"
                    value={formData.withdraw}
                    onChange={handleChange}
                  />
                </div>
                <div class="col-md-4">
                <label class="form-label">Withdraw Pirce</label>
                  <input
                    type="text"
                    class="form-control"
                    placeholder="price"
                    name="price"
                    value={formData.price}
                    onChange={handleChange}
                  />
                </div>
                
                <div class="col-md-4">
                <label class="form-label">Withdraw Currency</label>
                  <select
                    type="text"
                    class="form-control"
                    placeholder="currency"
                    name="currency"
                    value={formData.currency}
                    onChange={handleChange}
                  >
                     <option value="BNB">BNB</option>
                     <option value="DIGIPT">DIGIPT</option>
                  </select>
                </div>
          
                <div class="col-md-4">
                <br/>
                <button class="btn btn-primary" onClick={handleSubmitSetFee}>Withdraw</button>
                </div>
          
       
           
                
              </div>

            </div>
          </div>
        </div>

        {/* <div class="card card-bg" style={{ width: '18rem' }}>
          <div class="card-body">
            <h5 class="card-title">Total Users</h5>
            <h6 class="card-subtitle">{stats.users}</h6>
          </div>
        </div> */}
      </div>
    </div>
  )
}

export default Setting
