import Web3 from 'web3'
import contractJson from '../../contracts/Market.json'
import { CONTRACT_ADDRESSS } from '../../constant/index'
import {getCurrentAccount} from './web3'
//const Digiplazawap = new window.web3.eth.Contract(Digiplazawap.abi, CONTRACT_ADDRESSS)
let web3 = null
let contract = null

const getContract = () => {
  return new Promise(async (resolve, reject) => {
    if (!window.ethereum) {
      reject('Connect Metamask')
    }
    if (!web3) {
      web3 = new Web3(window.ethereum)
    }
    let contract = await new web3.eth.Contract(
      contractJson.abi,
      CONTRACT_ADDRESSS,
    )

    resolve(contract)
  })
}


export const getAuction = (tokenId) => {
  return new Promise(async (resolve, reject) => {
    try {
      if (!contract) {
        contract = await getContract()
      }
      let {active, creator, value, endTime, owner} = await contract.methods.getAuction(tokenId).call()
      const data = {
        active,
        creator: creator.toLowerCase(),
        value: parseInt(value),
        endTime,
        owner: owner.toLowerCase(),
      }
      resolve(data)
    } catch (err) {
      console.log(err)
      reject(err)
    }
  })
}


export const setAuctionFee = (fee,  tokenFee) => {
  return new Promise(async (resolve, reject) => {
    try {
      if (!contract) {
        contract = await getContract()
      }
      let publicAddress = await getCurrentAccount()

      
      let settedFree = await contract.methods
      .setFee(fee,  tokenFee)
      .send({ from: publicAddress})
      .on('transactionHash', function (hash) {})
      .on('confirmation', function (confirmationNumber, receipt) {})
      .on('receipt', function (receipt) {})
      
      resolve(settedFree)
    } catch (err) {
      console.log(err)
      reject(err)
    }
  })
}


export const withdraw = (addres,price, currency) => {
  return new Promise(async (resolve, reject) => {
    try {
      if (!contract) {
        contract = await getContract()
      }
      let publicAddress = await getCurrentAccount()

      let withdrawedTx = await contract.methods
      .withdraw(addres,  price, currency === "BNB"? '0': '1')
      .send({ from: publicAddress})
      .on('transactionHash', function (hash) {})
      .on('confirmation', function (confirmationNumber, receipt) {})
      .on('receipt', function (receipt) {})    
      resolve(withdrawedTx)
      
    } catch (err) {
      console.log(err)
      reject(err)
    }
  })
}

